import React, { useState } from "react";
import Header from "./Header";
import CreateEmp from "./CreateEmp";
import CreateCard from "./CreateCard";
const App = () =>{
    const [addItem,setAddItem]=useState([]);
    const addNote=(note)=>{
        //alert("I am clicked");
        setAddItem((prevData)=>{
            return[...prevData,note]
        });

    };

    const onDelete=(id)=>{
        setAddItem((oldData)=>
        oldData.filter((currdata,indx) =>{
            return indx!==id;
        })
        )
    };

       return(
           <>
            <Header/>
            <CreateEmp passNote={addNote}/>
            
            {addItem.map((val,index)=>{
                return <CreateCard
                    key={index}
                    id={index}
                    title={val.title}
                    deleteItem={onDelete}
                />
            })}
            
            
           </>
       );
};
export default App;



